import React from 'react';
import './Statistic.css';
import { Button, ModalCard, ModalRoot, Group, Cell, Switch } from '@vkontakte/vkui';
import stat from '../../img/stat.jpg'
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { toggleStatistic } from '../../store/questions/questions.store';

const Statistic = (props) => {
  const wichModal = useSelector(state => state.questions.statistic, shallowEqual);
  const dispatch =  useDispatch();
  const YesOrNot = (
    <>
      <span className='statistic__yes'>12 312 123</span>
      /
      <span className='statistic__not'>21 123</span>
    </>
  )

  const modalClose = id => {
    dispatch(toggleStatistic(null));
  }

  return (
    <ModalRoot activeModal={wichModal} onClose={modalClose}>
      <ModalCard id="statistic" onClose={modalClose}>
        <div className='statistic'>
          <div className='statistic__inner'>
            <div className='statistic__icon'>
              <img src={stat}/>
            </div>
            <div className='statistic__title'>Статистика по вопросу</div>
            <div className='statistic__subtitle'>Отключить статистику вы можете в настройках</div>
            <div className='statistic__content'>
              <Group>
                <Cell asideContent={YesOrNot}>За/Против</Cell>
                <Cell asideContent={<span className='statistic__like'>12 312</span>}>Лайки</Cell>
                <Cell asideContent={<Switch defaultChecked/>}> Показывать статистику после вопроса </Cell>
              </Group>
            </div>
            <Button size='xl' onClick={modalClose}>Следующий вопрос</Button>
          </div>
        </div>
      </ModalCard>
    </ModalRoot>
  );
};

export default Statistic;