import { getQuestions } from "../../api";
import { appLoaded, appLoading } from "../app/app.store";

const { createSlice, createAsyncThunk } = require("@reduxjs/toolkit");

const initialStateQuestions = {
  data: [],
  statistic: null,
};

const fetchQuestions = createAsyncThunk(
  'getQuestions',
  async (_, {dispatch}) => {
    try{
      dispatch(appLoading());
      const res = await getQuestions();
      return res.data
    }
    catch(e) {
      console.log(e);
      dispatch(appLoaded());
    }
  }
)

const questionsSlice = createSlice({
  name: "questions",
  initialState: initialStateQuestions,
  reducers: {
    addNewQuestion: (state, action) => state.data.push(action.payload),
    toggleStatistic: (state, action) => {state.statistic = action.payload},
  },
  extraReducers: {
    [fetchQuestions.fulfilled]: (state, action) => {
      state.data = action.payload;
    }
  }
})

export const {
  addNewQuestion,
  toggleStatistic
} = questionsSlice.actions

export default questionsSlice.reducer;

export {
  fetchQuestions,
}